      <div class="modal-wrapper">
        <div class="modal-container" :style="modalContainerStyle">
          <div v-if="!hideHeader" class="modal-header" :style="headerStyle">
            <slot name="header">default header</slot>
          </div>

          <div v-if="!hideBody" class="modal-body" :style="bodyStyle">
            <slot name="body">default body</slot>
          </div>

          <div v-if="!hideFooter" class="modal-footer" :style="footerStyle">
            <slot name="footer">default footer</slot>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: "modal",
  props: {
    width: {
      type: String,
      default: "300px"
    },
    containerStyle: {
      type: Object,
      default: () => {
        return {};
      }
    },
    hideHeader: {
      type: Boolean,
      default: false
    },
    hideBody: {
      type: Boolean,
      default: false
    },
    hideFooter: {
      type: Boolean,
      default: false
    },
    headerStyle: {
      type: String | Object,
      default: ""
    },
    bodyStyle: {
      type: String | Object,
      default: ""
    },
    footerStyle: {
      type: String | Object,
      default: ""
    }
  },
  data() {
    return {
      isShow: false,
      modalContainerStyle: {}
    };
  },
  created() {
    this.modalContainerStyle = Object.assign(
      { width: this.width },
      this.containerStyle
    );
  }
};
</script>

